import { parseMultipartStream } from "mixpart";
import { getVercelOidcToken } from "./oidc";
import type {
  ChangeVisibilityOptions,
  ChangeVisibilityResponse,
  DeleteMessageOptions,
  DeleteMessageResponse,
  Message,
  QueueClientOptions,
  ReceiveMessageByIdOptions,
  ReceiveMessageByIdResponse,
  ReceiveMessagesOptions,
  SendMessageOptions,
  SendMessageResponse,
  Transport,
} from "./types";
import {
  BadRequestError,
  ForbiddenError,
  InternalServerError,
  InvalidLimitError,
  MessageCorruptedError,
  MessageLockedError,
  MessageNotAvailableError,
  MessageNotFoundError,
  QueueEmptyError,
  UnauthorizedError,
} from "./types";

/**
 * Check if debug logging is enabled via environment variable
 */
function isDebugEnabled(): boolean {
  return (
    process.env.VERCEL_QUEUE_DEBUG === "1" ||
    process.env.VERCEL_QUEUE_DEBUG === "true"
  );
}

/**
 * Helper function to consume a ReadableStream to completion
 * This is necessary when we need to drain a stream to let the multipart parser proceed
 */
async function consumeStream(
  stream: ReadableStream<Uint8Array>,
): Promise<void> {
  const reader = stream.getReader();
  try {
    while (true) {
      const { done } = await reader.read();
      if (done) break;
    }
  } finally {
    reader.releaseLock();
  }
}

/**
 * Helper function to parse the Retry-After header value
 */
function parseRetryAfter(headers: Headers): number | undefined {
  const retryAfterHeader = headers.get("Retry-After");
  if (retryAfterHeader) {
    const parsed = parseInt(retryAfterHeader, 10);
    return Number.isNaN(parsed) ? undefined : parsed;
  }
  return undefined;
}

/**
 * Helper function to throw common HTTP errors (400, 401, 403, 500+)
 * Call this after checking for any method-specific error codes (404, 409, 423)
 */
function throwCommonHttpError(
  status: number,
  statusText: string,
  errorText: string,
  operation: string,
  badRequestDefault: string = "Invalid parameters",
): never {
  if (status === 400) {
    throw new BadRequestError(errorText || badRequestDefault);
  }
  if (status === 401) {
    throw new UnauthorizedError(errorText || undefined);
  }
  if (status === 403) {
    throw new ForbiddenError(errorText || undefined);
  }
  if (status >= 500) {
    throw new InternalServerError(
      errorText || `Server error: ${status} ${statusText}`,
    );
  }
  throw new Error(`Failed to ${operation}: ${status} ${statusText}`);
}

/**
 * Helper function to parse queue headers into a partial message object
 * Handles both multipart message headers and HTTP response headers
 */
function parseQueueHeaders(
  headers: Headers,
): Omit<Message<unknown>, "payload"> | null {
  const messageId = headers.get("Vqs-Message-Id");
  const deliveryCountStr = headers.get("Vqs-Delivery-Count") || "0";
  const timestamp = headers.get("Vqs-Timestamp");
  const contentType = headers.get("Content-Type") || "application/octet-stream";
  const ticket = headers.get("Vqs-Ticket");

  if (!messageId || !timestamp || !ticket) {
    return null;
  }

  const deliveryCount = parseInt(deliveryCountStr, 10);
  if (Number.isNaN(deliveryCount)) {
    return null;
  }

  return {
    messageId,
    deliveryCount,
    createdAt: new Date(timestamp),
    contentType,
    ticket,
  };
}

/**
 * Internal client for interacting with the Vercel Queue Service API
 * Use Client for the public-facing wrapper with send/handleCallback methods
 */
export class QueueClient {
  private baseUrl: string;
  private basePath: string;
  private customHeaders: Record<string, string>;
  private providedToken?: string;

  /**
   * Create a new Vercel Queue Service client
   * @param options QueueClient configuration options
   */
  constructor(options: QueueClientOptions = {}) {
    this.baseUrl =
      options.baseUrl ||
      process.env.VERCEL_QUEUE_BASE_URL ||
      "https://vercel-queue.com";
    this.basePath =
      options.basePath ||
      process.env.VERCEL_QUEUE_BASE_PATH ||
      "/api/v2/messages";
    this.customHeaders = options.headers || {};
    this.providedToken = options.token;
  }

  private async getToken(): Promise<string> {
    // Use provided token if available
    if (this.providedToken) {
      return this.providedToken;
    }

    const token = await getVercelOidcToken();
    if (!token) {
      throw new Error(
        "Failed to get OIDC token from Vercel Functions. " +
          "Make sure you are running in a Vercel Function environment, or provide a token explicitly.\n\n" +
          "To set up your environment:\n" +
          "1. Link your project: 'vercel link'\n" +
          "2. Pull environment variables: 'vercel env pull'\n" +
          "3. Run with environment: 'dotenv -e .env.local -- your-command'",
      );
    }
    return token;
  }

  /**
   * Internal fetch wrapper that automatically handles debug logging
   * when VERCEL_QUEUE_DEBUG is enabled
   */
  private async fetch(
    url: string,
    init: RequestInit & { headers: Headers },
  ): Promise<Response> {
    const method = init.method || "GET";

    // Log request if debug is enabled
    if (isDebugEnabled()) {
      const logData: Record<string, unknown> = {
        method,
        url,
        headers: init.headers,
      };

      // Log body size/type if present (don't log actual body content for privacy)
      const body = init.body;
      if (body !== undefined && body !== null) {
        if (body instanceof ArrayBuffer) {
          logData.bodySize = body.byteLength;
        } else if (body instanceof Uint8Array) {
          logData.bodySize = body.byteLength;
        } else if (typeof body === "string") {
          logData.bodySize = body.length;
        } else {
          logData.bodyType = typeof body;
        }
      }

      console.debug("[VQS Debug] Request:", JSON.stringify(logData, null, 2));
    }

    const response = await fetch(url, init);

    // Log response if debug is enabled
    if (isDebugEnabled()) {
      const logData: Record<string, unknown> = {
        method,
        url,
        status: response.status,
        statusText: response.statusText,
        headers: response.headers,
      };

      console.debug("[VQS Debug] Response:", JSON.stringify(logData, null, 2));
    }

    return response;
  }

  /**
   * Send a message to a queue
   * @param options Send message options
   * @param transport Serializer/deserializer for the payload
   * @returns Promise with the message ID
   * @throws {BadRequestError} When request parameters are invalid
   * @throws {UnauthorizedError} When authentication fails
   * @throws {ForbiddenError} When access is denied (environment mismatch)
   * @throws {InternalServerError} When server encounters an error
   */
  async sendMessage<T = unknown>(
    options: SendMessageOptions<T>,
    transport: Transport<T>,
  ): Promise<SendMessageResponse> {
    const { queueName, payload, idempotencyKey, retentionSeconds } = options;

    const headers = new Headers({
      Authorization: `Bearer ${await this.getToken()}`,
      "Vqs-Queue-Name": queueName,
      "Content-Type": transport.contentType,
      ...this.customHeaders,
    });

    const deploymentId =
      options.deploymentId || process.env.VERCEL_DEPLOYMENT_ID;
    if (deploymentId) {
      headers.set("Vqs-Deployment-Id", deploymentId);
    }

    if (idempotencyKey) {
      headers.set("Vqs-Idempotency-Key", idempotencyKey);
    }

    if (retentionSeconds !== undefined) {
      headers.set("Vqs-Retention-Seconds", retentionSeconds.toString());
    }

    // Serialize the payload using the provided transport
    const body = transport.serialize(payload);

    const response = await this.fetch(`${this.baseUrl}${this.basePath}`, {
      method: "POST",
      body,
      headers,
    });

    if (!response.ok) {
      const errorText = await response.text();
      // Method-specific: 409 for duplicate idempotency key
      if (response.status === 409) {
        throw new Error("Duplicate idempotency key detected");
      }
      throwCommonHttpError(
        response.status,
        response.statusText,
        errorText,
        "send message",
      );
    }

    const responseData = (await response.json()) as SendMessageResponse;

    return responseData;
  }

  /**
   * Receive messages from a queue
   * @param options Receive messages options
   * @param transport Serializer/deserializer for the payload
   * @returns AsyncGenerator that yields messages as they arrive
   * @throws {InvalidLimitError} When limit parameter is not between 1 and 10
   * @throws {QueueEmptyError} When no messages are available (204)
   * @throws {MessageLockedError} When messages are temporarily locked (423)
   * @throws {BadRequestError} When request parameters are invalid
   * @throws {UnauthorizedError} When authentication fails
   * @throws {ForbiddenError} When access is denied (environment mismatch)
   * @throws {InternalServerError} When server encounters an error
   */
  async *receiveMessages<T = unknown>(
    options: ReceiveMessagesOptions<T>,
    transport: Transport<T>,
  ): AsyncGenerator<Message<T>, void, unknown> {
    const { queueName, consumerGroup, visibilityTimeoutSeconds, limit } =
      options;

    // Validate limit parameter
    if (limit !== undefined && (limit < 1 || limit > 10)) {
      throw new InvalidLimitError(limit);
    }

    const headers = new Headers({
      Authorization: `Bearer ${await this.getToken()}`,
      "Vqs-Queue-Name": queueName,
      "Vqs-Consumer-Group": consumerGroup,
      Accept: "multipart/mixed",
      ...this.customHeaders,
    });

    if (visibilityTimeoutSeconds !== undefined) {
      headers.set(
        "Vqs-Visibility-Timeout",
        visibilityTimeoutSeconds.toString(),
      );
    }

    if (limit !== undefined) {
      headers.set("Vqs-Limit", limit.toString());
    }

    const response = await this.fetch(`${this.baseUrl}${this.basePath}`, {
      method: "GET",
      headers,
    });

    // Check for 204 No Content - queue is empty
    if (response.status === 204) {
      throw new QueueEmptyError(queueName, consumerGroup);
    }

    if (!response.ok) {
      const errorText = await response.text();
      // Method-specific: 423 for message locked
      if (response.status === 423) {
        throw new MessageLockedError(
          "next message",
          parseRetryAfter(response.headers),
        );
      }
      throwCommonHttpError(
        response.status,
        response.statusText,
        errorText,
        "receive messages",
      );
    }

    // Stream messages as they arrive from the multipart parser
    // Each message's payload stream must be consumed immediately for the parser to proceed
    for await (const multipartMessage of parseMultipartStream(response)) {
      try {
        // Parse VQS headers using the helper function
        const parsedHeaders = parseQueueHeaders(multipartMessage.headers);

        if (!parsedHeaders) {
          console.warn("Missing required queue headers in multipart part");
          // Still need to consume the payload stream to let the parser proceed
          await consumeStream(multipartMessage.payload);
          continue;
        }

        // Deserialize using the provided transport (must consume the stream)
        const deserializedPayload = await transport.deserialize(
          multipartMessage.payload,
        );

        const message: Message<T> = {
          ...parsedHeaders,
          payload: deserializedPayload,
        };

        yield message;
      } catch (error) {
        console.warn("Failed to process multipart message:", error);
        // If deserialization failed, we still need to consume the payload stream
        // to let the multipart parser proceed to the next message
        await consumeStream(multipartMessage.payload);
      }
    }
  }

  /**
   * Receive a specific message by its ID from a queue
   * @param options Receive message by ID options
   * @param transport Serializer/deserializer for the payload
   * @returns Promise with the message or null if not found/available
   * @throws {MessageNotFoundError} When the message doesn't exist (404)
   * @throws {MessageLockedError} When the message is temporarily locked (423)
   * @throws {MessageNotAvailableError} When message exists but isn't available (409)
   * @throws {MessageCorruptedError} When message data is corrupted
   * @throws {BadRequestError} When request parameters are invalid
   * @throws {UnauthorizedError} When authentication fails
   * @throws {ForbiddenError} When access is denied (environment mismatch)
   * @throws {InternalServerError} When server encounters an error
   */
  async receiveMessageById<T = unknown>(
    options: ReceiveMessageByIdOptions<T> & { skipPayload: true },
    transport?: Transport<T>,
  ): Promise<ReceiveMessageByIdResponse<T, true>>;
  async receiveMessageById<T = unknown>(
    options: ReceiveMessageByIdOptions<T> & { skipPayload?: false | undefined },
    transport: Transport<T>,
  ): Promise<ReceiveMessageByIdResponse<T, false>>;
  async receiveMessageById<T = unknown>(
    options: ReceiveMessageByIdOptions<T>,
    transport?: Transport<T>,
  ): Promise<ReceiveMessageByIdResponse<T, boolean>> {
    const {
      queueName,
      consumerGroup,
      messageId,
      visibilityTimeoutSeconds,
      skipPayload,
    } = options;

    const headers = new Headers({
      Authorization: `Bearer ${await this.getToken()}`,
      "Vqs-Queue-Name": queueName,
      "Vqs-Consumer-Group": consumerGroup,
      Accept: "multipart/mixed",
      ...this.customHeaders,
    });

    if (visibilityTimeoutSeconds !== undefined) {
      headers.set(
        "Vqs-Visibility-Timeout",
        visibilityTimeoutSeconds.toString(),
      );
    }

    if (skipPayload) {
      headers.set("Vqs-Skip-Payload", "1");
    }

    const response = await this.fetch(
      `${this.baseUrl}${this.basePath}/${encodeURIComponent(messageId)}`,
      {
        method: "GET",
        headers,
      },
    );

    if (!response.ok) {
      const errorText = await response.text();
      // Method-specific error handling
      if (response.status === 404) {
        throw new MessageNotFoundError(messageId);
      }
      if (response.status === 409) {
        throw new MessageNotAvailableError(messageId);
      }
      if (response.status === 423) {
        throw new MessageLockedError(
          messageId,
          parseRetryAfter(response.headers),
        );
      }
      throwCommonHttpError(
        response.status,
        response.statusText,
        errorText,
        "receive message by ID",
      );
    }

    // Handle skipPayload case with 204 response
    if (skipPayload && response.status === 204) {
      const parsedHeaders = parseQueueHeaders(response.headers);

      if (!parsedHeaders) {
        throw new MessageCorruptedError(
          messageId,
          "Missing required queue headers in 204 response",
        );
      }

      const message: Message<void> = {
        ...parsedHeaders,
        payload: undefined as void,
      };

      return { message } as ReceiveMessageByIdResponse<T, boolean>;
    }

    // Handle regular multipart response
    if (!transport) {
      throw new Error("Transport is required when skipPayload is not true");
    }

    // Parse multipart/mixed response using streaming parser
    try {
      for await (const multipartMessage of parseMultipartStream(response)) {
        try {
          // Parse queue headers using the helper function
          const parsedHeaders = parseQueueHeaders(multipartMessage.headers);

          if (!parsedHeaders) {
            console.warn("Missing required queue headers in multipart part");
            // Still need to consume the payload stream to let the parser proceed
            await consumeStream(multipartMessage.payload);
            continue;
          }

          // Deserialize using the provided transport (must consume the stream)
          const deserializedPayload = await transport.deserialize(
            multipartMessage.payload,
          );

          const message: Message<T> = {
            ...parsedHeaders,
            payload: deserializedPayload,
          };

          return { message };
        } catch (error) {
          console.warn("Failed to deserialize message by ID:", error);
          // If deserialization failed, we still need to consume the payload stream
          // to let the multipart parser proceed to the next message
          await consumeStream(multipartMessage.payload);
          throw new MessageCorruptedError(
            messageId,
            `Failed to deserialize payload: ${error}`,
          );
        }
      }
    } catch (error) {
      if (error instanceof MessageCorruptedError) {
        throw error; // Re-throw our own errors
      }
      throw new MessageCorruptedError(
        messageId,
        `Failed to parse multipart response: ${error}`,
      );
    }

    // If we get here, no message was found in the multipart response
    throw new MessageNotFoundError(messageId);
  }

  /**
   * Delete a message (acknowledge processing)
   * @param options Delete message options
   * @returns Promise with delete status
   * @throws {MessageNotFoundError} When the message doesn't exist (404)
   * @throws {MessageNotAvailableError} When message can't be deleted (409)
   * @throws {BadRequestError} When ticket is missing or invalid (400)
   * @throws {UnauthorizedError} When authentication fails
   * @throws {ForbiddenError} When access is denied (environment mismatch)
   * @throws {InternalServerError} When server encounters an error
   */
  async deleteMessage(
    options: DeleteMessageOptions,
  ): Promise<DeleteMessageResponse> {
    const { queueName, consumerGroup, messageId, ticket } = options;

    const response = await this.fetch(
      `${this.baseUrl}${this.basePath}/${encodeURIComponent(messageId)}`,
      {
        method: "DELETE",
        headers: new Headers({
          Authorization: `Bearer ${await this.getToken()}`,
          "Vqs-Queue-Name": queueName,
          "Vqs-Consumer-Group": consumerGroup,
          "Vqs-Ticket": ticket,
          ...this.customHeaders,
        }),
      },
    );

    if (!response.ok) {
      const errorText = await response.text();
      // Method-specific error handling
      if (response.status === 404) {
        throw new MessageNotFoundError(messageId);
      }
      if (response.status === 409) {
        throw new MessageNotAvailableError(
          messageId,
          errorText ||
            "Invalid ticket, message not in correct state, or already processed",
        );
      }
      throwCommonHttpError(
        response.status,
        response.statusText,
        errorText,
        "delete message",
        "Missing or invalid ticket",
      );
    }

    return { deleted: true };
  }

  /**
   * Change the visibility timeout of a message
   * @param options Change visibility options
   * @returns Promise with update status
   * @throws {MessageNotFoundError} When the message doesn't exist (404)
   * @throws {MessageNotAvailableError} When message can't be updated (409)
   * @throws {BadRequestError} When ticket is missing or visibility timeout invalid (400)
   * @throws {UnauthorizedError} When authentication fails
   * @throws {ForbiddenError} When access is denied (environment mismatch)
   * @throws {InternalServerError} When server encounters an error
   */
  async changeVisibility(
    options: ChangeVisibilityOptions,
  ): Promise<ChangeVisibilityResponse> {
    const {
      queueName,
      consumerGroup,
      messageId,
      ticket,
      visibilityTimeoutSeconds,
    } = options;

    const response = await this.fetch(
      `${this.baseUrl}${this.basePath}/${encodeURIComponent(messageId)}`,
      {
        method: "PATCH",
        headers: new Headers({
          Authorization: `Bearer ${await this.getToken()}`,
          "Vqs-Queue-Name": queueName,
          "Vqs-Consumer-Group": consumerGroup,
          "Vqs-Ticket": ticket,
          "Vqs-Visibility-Timeout": visibilityTimeoutSeconds.toString(),
          ...this.customHeaders,
        }),
      },
    );

    if (!response.ok) {
      const errorText = await response.text();
      // Method-specific error handling
      if (response.status === 404) {
        throw new MessageNotFoundError(messageId);
      }
      if (response.status === 409) {
        throw new MessageNotAvailableError(
          messageId,
          errorText ||
            "Invalid ticket, message not in correct state, or already processed",
        );
      }
      throwCommonHttpError(
        response.status,
        response.statusText,
        errorText,
        "change visibility",
        "Missing ticket or invalid visibility timeout",
      );
    }

    return { updated: true };
  }
}
